button {
  position: relative;
  padding: 0.375rem 0.88rem;
  color: #333;
  background-color: #ddd;
  border-color: #ddd;
  font-weight: 400;
  text-align: left;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  font-size: 0.85rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  cursor: pointer;
}

button:hover {
  color: #fff;
  background-color: #0069d9;
}

button.active {
  color: #fff;
  background-color: #0069d9;
}

.announcement {
  padding: 0 0 0.88rem;
  margin: 0 0 0.88rem;
  border-bottom: 1px solid rgb(226, 226, 226);
}